import * as React from "react";

import Link from "@mui/material/Link";
import Typography from "@mui/material/Typography";
import Breadcrumbs from "@mui/material/Breadcrumbs";
import HouseIcon from "@mui/icons-material/House";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";

import { Link as RouterLink, useLocation } from "react-router-dom";

const breadcrumbNameMap = {
  "/dashboard": "Dashboard",

  "/dashboard/booking/list": "Booking-list",
  "/dashboard/booking/detail": "Booking-detail",
  
  "dashboard/profile": "Profile",
};

const LinkRouter = (props) => (
  <Link variant="body2" {...props} component={RouterLink} />
);

const PathBreadcrumbs = () => {
  const location = useLocation();
  const pathnames = location.pathname.split("/").filter((x) => x);

  return (
    <Breadcrumbs
      aria-label="breadcrumb"
      separator={
        <FiberManualRecordIcon
          sx={{ fontSize: "5px", color: "text.secondary" }}
        />
      }
      maxItems={2}
    >
      <LinkRouter
        underline="hover"
        color="secondary"
        to="/"
        className="d-inline-block"
      >
        <HouseIcon className="align-top" sx={{ fontSize: "20px" }} />
      </LinkRouter>
      {pathnames.map((value, index) => {
        const last = Boolean(index === pathnames.length - 1);
        const to = `/${pathnames.slice(0, index + 1).join("/")}`;
        return last ? (
          <Typography
            variant="body2"
            color="text.disabled"
            key={to}
            className="fw-bold"
          >
            {breadcrumbNameMap[to]}
          </Typography>
        ) : (
          <LinkRouter className="text-decoration-none fw-bold" color="secondary" to={to} key={to}>
            {breadcrumbNameMap[to]}
          </LinkRouter>
        );
      })}
    </Breadcrumbs>
  );
};

export default PathBreadcrumbs;
